











































































































































































































































































































import { Component, Vue } from 'vue-property-decorator';
import {HttpService} from "@/services/ApiService";
import { Direction, ServiciosInterface} from "@/models/service";
import DatePicker from 'vue2-datepicker';
import 'vue2-datepicker/index.css';
import 'vue2-datepicker/locale/es';
import Chip from "@/components/chip.vue";

// @ts-ignore
import VueTimepicker from 'vue2-timepicker'

//import gql from 'graphql-tag'
import { shippingUpdatedAll} from "@/graphql/subscription";


// Import stylesheet
import CardShipping from "@/components/services/CardShipping.vue";
import {ShippingModel} from "@/models/shipping";
import _ from 'lodash';
import gql from 'graphql-tag';

@Component({
  components:{
    CardShipping,
    DatePicker,
    Chip,
    VueTimepicker,
  }
})
export default class Service extends Vue {

  public services: ServiciosInterface[] = [];

  public vermas: number[] = [];

  public estado: number[]| null = [0,1,2,3,4,5,6,7,8];

  public status: number|null = null;

  public reference = '';
  public search_repartidor = '';

  public direction: number | null = null;

  getTotalGroupService(ShippingModel: ShippingModel[]): number {
    const reducer = (previousValue, currentValue) => previousValue + currentValue;

    let totals = ShippingModel.map((value, index, array) => {
      let amount = 0;
      if(value.type_delivery == 0){
        amount = value.total ?? 0;
      }else{
        if(value.payment_location){
          amount = value.total ?? 0;
        }
      }
      return amount;
    });

   return totals.reduce(reducer);

  }

  public lang = {
    formatLocale: {
      firstDayOfWeek: 1,
    },
    monthBeforeYear: false,
  };

  public radio: null|number = null;
  public mensajero: null|number = null;

  public repartidoresList: any[] = [];

  public serviceId: number|null  = null;


  public directions = [];


  range: Date[] = [];




  public groupBy: null|string = null;

  groupShippings(shippings: ShippingModel[]){
    switch (this.groupBy) {
       case "service":
         return _.groupBy(shippings, "shipping_service_id")
      case "user":
        return _.groupBy(shippings, "user.id")
      case "dboy":
        return _.groupBy(shippings, "dboy_id")
      default:
        return _.groupBy(shippings, "shipping_service_id")
    }


  }


  verificarElStatusTitle(dir: Direction){
    let data = dir.address_exchange.find((e) => e.status == 1);

    return !!data;
  }

  verificarSiesEntrada(dir: Direction){
    let data = dir.address_exchange.find((e) => e.flow == 1);

    return !!data;
  }




  setRepartidores(data: any){
    console.log(data)
    this.repartidoresList = data.data;
  }

  toggleEstado(id: number){
    console.log(id)
    if(id == null) {
      this.estado = [0,1,2,3,4,5,6,7,8];
    }else{
      this.estado = [id];
    }
  }

  setItem(data:any){
    let service: ServiciosInterface = data;
    console.log(service)
    let index = this.services.findIndex(e => e.id == service.id);
    if(index != undefined && index > -1){
      //this.deliveries.splice(index, 1, service)
      console.log(index);
      this.$set(this.services, index, service)
    }
  }


  openDialog(id: any){
    console.log(id)
    this.serviceId = id;
    this.repartidoresList = [];
    this.mensajero = null;
    this.radio = null;

    // @ts-ignore
    this.$refs.modal.open()
  }



  get miId(){
    return this.vermas.find(e => e === 1)
  }

  toggleId(id: number){
    if(this.vermas.find(e => e === id) == undefined){

      this.vermas.push(id)
    }else{
      let index = this.vermas.findIndex(e => e === id)
      this.vermas.splice(index, 1)
    }
  }



  formatDate(date: Date) {
    let d = new Date(date),
        month = '' + (d.getMonth() + 1),
        day = '' + d.getDate(),
        year = d.getFullYear();

    if (month.length < 2)
      month = '0' + month;
    if (day.length < 2)
      day = '0' + day;

    return [year, month, day].join('-');
  }

  toggleEstadoArray(status: number[]){
    if(status.length > 0){
      this.estado = status;
    }
  }



  asignarDboy(){

    this.$apollo.mutate({
      mutation: gql`mutation($id: ID! $dboy_id: ID!) {
        assignDboyShippingAdmin(id: $id dboy_id: $dboy_id)
      }`,
      variables: {
        "id" : this.serviceId,
        "dboy_id": this.mensajero
      }
    }).then((res) => {
      if(res.data){
        this.$alert(res.data.assignDboyShippingAdmin, "¡Exito!", "success")
      }
    }).catch((e) =>{
      this.$alert("Ha ocurrido un error", "¡Error!", "error")

    })

    // @ts-ignore
    this.$refs.modal.close();



  }


  getRepartidoresDisponibles(){
    this.mensajero = null;
    HttpService.post(`repartidores-disponibles`,
        {
          'search': this.search_repartidor,
          'radio': this.radio,
          'id': this.serviceId,
        },
        this.setRepartidores)
  }

  getToken() :string{
    let vuex = localStorage.getItem('vuex')

    if(vuex){
      return JSON.parse(vuex).profile?.token ?? ''
    }else{
      return ''
    }
  }


  created(){
    // this.$apollo.query({
    //   query: gql`query {
    //     users(first: 10){
    //       data{
    //         email
    //       }
    //     }
    //   }`,
    // })

   console.log(this.getToken())

    const observer = this.$apollo.subscribe({
      query: shippingUpdatedAll,
    })
    observer.subscribe({
      next (data) {
        console.log(data)
      },
      error (error) {
        console.log(error)
      },
    })


    const date = new Date()

    let dateOffset = (24*60*60*1000) * 30;
    let myDate = new Date();
    myDate.setTime(myDate.getTime() - dateOffset);
    this.range.push(myDate)
    this.range.push(date)


  }
}
