

























































































































































































































































import {Component, Prop, Vue} from "vue-property-decorator";
import DatePicker from "vue2-datepicker";
import Chip from "@/components/chip.vue";
import 'vue2-datepicker/index.css';
import 'vue2-datepicker/locale/es';


// @ts-ignore
import VueTimepicker from 'vue2-timepicker'
import gql from "graphql-tag";
import {ShippingModel} from "@/models/shipping";

@Component({
  components:{
    DatePicker,
    Chip,
    VueTimepicker,
  }
})
export default class CardShipping extends Vue {

  @Prop({required: true, type: Object}) shipping: ShippingModel | undefined;

  public urlImg = 'https://todi-upload.s3.amazonaws.com/upload/avatar/';
  public urlImgDboy = 'https://todi-upload.s3.amazonaws.com/upload/dboys/';

  public vermas =false;

  @Prop({required:true, type: Function, }) asignarMensajero: Function | undefined;



  public lang = {
    formatLocale: {
      firstDayOfWeek: 1,
    },
    monthBeforeYear: false,
  };

  getDiffHour(estimate: any, date: any){

// @ts-ignore
    return this.$moment(estimate).diff(date, 'minutes') + ' minutos'
  }


  changeStatus(id: string, status: number){
    this.$apollo.mutate({
      // Query
      mutation: gql`mutation ($status: Int! $id: ID! ) {
         updatedShipping(id: $id input:{
            status:$status
          }
          ){
            id
            status
          }
      }`,
      // Parameters
      variables: {
        id: id,
        status: status,
      },
    }).then((data) => {
      // Result
      console.log(data)
    }).catch((error) => {
      // Error
      console.error(error)
    })
  }

  textTypeService(type: number){
    switch (type) {
      case 0:
        return "Inicial"
      case 1:
        return "Entrega"
      case 2:
        return "Devolucion"
    }
  }

  textStatus(status: number){
    switch (status) {
      case 0:
      case 7:
        return 'Cancelado'
      case 1:
        return 'En proceso'
      case 2:
        return "Recogido"
      case 3:
        return "En camino"
      case 4:
        return "Entregado"
      case 6:
        return "Devolución"
      case 8:
        return "Esperando otro repartidor"

    }
  }

}
